export const MILEAGE = setMileage();
function setMileage() {
  let mileage = [];
  for (let i = 10; i <= 300; i += 10) {
    mileage.push({ id: i, value: ` ${i} .` });
  }
  for (let i = 350; i <= 500; i += 50) {
    mileage.push({ id: i, value: ` ${i} .` });
  }
  return mileage;
}

